import React from "react";
import { useSpring, animated } from 'react-spring'

const calc = (x, y) => [-(y - window.innerHeight / 2) / 40, (x - window.innerWidth / 2) / 40, 1.1]
const trans = (x, y, s) => `perspective(400px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`

function Explainer() {
  const [props, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 40 } }))
  return (
    <div className="flex-1 background flex justify-center items-center h-auto">
        <animated.div
          className="w-1/2 h-xxxxl bg-grey"
          onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
          onMouseLeave={() => set({ xys: [0, 0, 1] })}
          style={{ transform: props.xys.interpolate(trans)}}
          src={'http://www.thegoldenelm.org/wp-content/uploads/2019/05/ebooklaunch_custom-book-cover-design-portfolio_500x800_non-fiction_onlinetoinperson.jpg'}
        />
    </div>
  )
}
  
  export default Explainer;